import axios from "axios";
import qs from "qs";

const showStatus = (status) => {
    const msg = {
        400: '(400)',
        401: ',(401)',
        403: '(403)',
        404: '(404)',
        408: '(408)',
        500: '(500)',
        501: '(501)',
        502: '(502)',
        503: '(503)',
        504: '(504)',
        505: 'HTTP(505)'
    };
    if (status in msg) {
        return `${msg[status]},`
    } else {
        return `(${status}),`
    }
};

const Service = axios.create({
    //     
    // baseURL: process.env.NODE_ENV === 'production' ? 'http://172.22.4.2:8080/':'http://172.22.4.2:8080/',
    baseURL: process.env.NODE_ENV === 'production' ? 'http://49.235.20.228:8080/':'http://49.235.20.228:8080/',
    //baseURL: 'http://localhost:8080/',
        headers: {
            get: {
                'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
            },
            post: {
                'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
                // 'Content-Type': 'application/json'
            }
        },
    timeout: 30000,
    transformRequest: [ data => {
        // data = JSON.stringify(data);
        if(Object.prototype.toString.call(data) !== '[object FormData]') data = qs.stringify(data);
        // data = JSON.stringify(data)
        // console.log(data.prototype);
        return data
    }],
    validateStatus () {
        // async-awaitrejectresolve
        return true
    },
    transformResponse: [(data) => {
        if (typeof data === 'string' && data.startsWith('{')) {
            data = JSON.parse(data)
        }
        return data
    }]
});
//
Service.interceptors.request.use(config => {
    const isServer = typeof window === 'undefined';
    if (!isServer) {
        const token = window.localStorage.getItem('access');
        if (token) {
            config.headers.common['token'] = token;
        }
    }
    return config
},error => {
    error.data = {};
    error.data.msg = '!';
    return Promise.resolve(error);
});
//
Service.interceptors.response.use(response => {
    const status = response.status;
    let message = '';
    if (status < 200 || status >= 300) {
        message = showStatus(status);
        if (typeof response.data === 'string') {
            response.data = { message };
        } else {
            response.data.message = message;
        }
    }
    return response;
},error => {
    error.data = {};
    error.data.msg = ',!';
    return Promise.resolve(error)
});

export default Service;
