import React from "react";
import { connect } from "react-redux";
import "../static/style/search.scss";
import { Input, Select } from 'antd';
import * as resource from "../redux/actionCreators/resource";

const InputGroup = Input.Group;
const { Option } = Select;
const { Search } = Input;
let flag = "teacherName";

const SearchCourse = props => {
    const { handleSearchCourse } = props;
    return(
        <div className="container">
            <InputGroup
                className="search"
                compact
            >
                <Select
                    defaultValue="teacherName"
                    onChange={value => flag = value }
                >
                    <Option value="teacherName"></Option>
                    <Option value="course"></Option>
                </Select>
                <Search
                    placeholder=""
                    onSearch={(value,e) => handleSearchCourse(
                        flag === "teacherName" ? {
                            page: 1,
                            teacherName: value,
                            course: "",
                            userId: "",
                        } : {
                            page: 1,
                            teacherName: "",
                            course: value,
                            userId: ""
                        }
                    )
                    }
                    enterButton
                />
            </InputGroup>
        </div>
    )
};

export default connect(
    state => ({

    }),
    dispatch => ({
        handleSearchCourse(args) {
            dispatch(resource.fetchSearchCourse(args))
        }
    })
)(SearchCourse)
